		bio: 'Tanya Gonzalez, originally from Puerto Rico, faced challenges due to political and socio-economic issues in her home country. She moved to Tennessee after graduating from Margarita Janer Palacios High School and pursued an online Associates degree in Business Administration from the Interamerican University of Puerto Rico. While studying, she worked as an Assistant Manager at a Dollar General store. Seeking new opportunities, Tanya enrolled at Middle Tennessee State University (MTSU) and earned a Bachelors degree in Criminal Justice with a specialization in Homeland Security, along with double minors in Asian Studies and Information Systems. Despite graduating during the COVID-19 pandemic in May 2020, she actively engaged in various extracurricular activities during her undergraduate years, serving as the Former President of La Comunidad and mentoring the Asian Student Association. She also participated in organizations such as the Caribbean Student Association, Hispanic and Asian Heritage Month Committee, LGBT+ events, and more.',
